/* eslint-disable */
import { RESPONSE_CODE } from '@/api/config'
import { getShopList } from '@/api/shop'
import { getTouTiaoByCategory } from '@/api/toutiao'
import { mapMutations } from 'vuex'

const size = 6 

export default {
  data () {
    return {
      page: 0,
      pullup: true,
      loading: true,
      shopList: [],
      tip: '努力加载中...'
    }
  },
  created (){
    this._getShopList();
  },
  methods: {
    _getShopList() {
      this.page = 0
      this.size = 6
      this.loading = true
      getShopList(this.page, this.size).then((res) => {

        if( !res) {
          this.loading = false
          this.tip = '网络不给力！'
          return;
        }

        if (res.code === RESPONSE_CODE) {

            this.shopList = res.data.list
            this.loading = res.data.hasNext

            this.tip = this.loading ? '努力加载中...' : '没有更多数据~'
        }
      })
    },
    upMore () {
      if (!this.loading) {
            return
        }
        getShopList(++this.page, this.size).then((res) => {

            if (res.code === RESPONSE_CODE) {

                this.shopList = this.shopList.concat(res.data.list)
                this.loading = res.data.hasNext

                this.tip = this.loading ? '努力加载中...' : '没有更多数据~'
            }
            
        })        
    },
    selectItem (item) {
      this.$router.push({
        path: `shop/${item.id}`,
      })
      this.setShop(item)
    },
    ...mapMutations({
        setShop: 'SET_SHOP'
    })
  }
}



