/* eslint-disable */
import { RESPONSE_CODE } from '@/api/config'
import { getCategoryList } from '@/api/category'
import { mapMutations } from 'vuex'
export default {
    data() {
        return {
            page: 0,
            size: 14,
            pullup: true,
            categoryList: [],
            loading: true,
            tip: '努力加载中...'
        }
    },
    created() {
        this._getCategory()
    },
    methods: {
        selectItem (item) {
            this.$router.push({
                path: `category/${item.id}`,
            })
            this.setNews(item)
        },
        downMore() {
            if (!this.loading) {
                return
            }
            this.page++
            getCategoryList(this.page, this.size).then((res) => {
                if (res.code === RESPONSE_CODE) {
                    this.categoryList = this.categoryList.concat(res.data.list)
                    this.loading = res.data.hasNext
                    this.tip = this.loading ? '努力加载中...' : '没有更多数据~'
                }
            })           
        },
        _getCategory() {
            this.page = 0
            this.loading = true
            getCategoryList(this.page, this.size).then((res) => {
                
                if( !res) {
                    this.loading = false
                    this.tip = '网络不给力！'
                    return;
                }

                if (res.code === RESPONSE_CODE) {
                    
                    this.categoryList = res.data.list
                    this.loading = res.data.hasNext

                    this.tip = this.loading ? '努力加载中...' : '没有更多数据~'
                    
                }
            })
        },
        ...mapMutations({
            setNews: 'SET_NEWS'
        })
    }
}



