import { setInterval, clearInterval } from 'timers'
/* eslint-disable */
export default {
    data(){
        return {
            contactShow: true
        }
    },
    methods:{
        subscribeUs(){
            this.$emit('subscribeUs')
        },
        contactUs(){
            this.$emit('contactUs')
        }
    },
    created(){
        this.timer = setInterval(()=>{
                this.contactShow = !this.contactShow
        },10000)
    },
    beforeDestroy() {
        clearInterval(this.timer)
    }
}



