
/* eslint-disable */
import Previewer from 'vux/src/components/previewer/index.vue'

import { getPostList } from '@/api/post'
import { RESPONSE_CODE } from '@/api/config'
export default {
data () {
return {
pullup: true,
postList: [],
loading: true,
tip: '加载中...',
previewerList: [],
options: {
index: 0
}

}
},
created(){
this._initPostList()
},
methods: {
onItem(pic){
if(pic.length === 0){
return
}
for(let i=0;i<pic.length;i++){
this.previewerList.push({
src: pic[i]
})
}
this.$refs.previewer.show(0)
},
onClose() {
this.previewerList = []
this.options.index =0
},
doCopy(value) {
this.$copyText(value).then(e=> {
this.$vux.toast.text('已复制联系方式', 'middle')
})
},
_initPostList(){
this.page = 0
this.size = 6
getPostList(this.page, this.size).then(res=>{
if( res.code === RESPONSE_CODE) {
this.postList = res.data.list
this.loading = res.data.hasNext
this.tip = this.loading ? '加载中...':'已加载全部数据~'
}
})
},
nextPostList() {
if(!this.loading){
return
}
getPostList(++this.page, this.size).then(res=>{
if( res.code === RESPONSE_CODE) {
this.postList = this.postList.concat(res.data.list)
this.loading = res.data.hasNext
this.tip = this.loading ? '加载中...':'已加载全部数据~'
}
})
},
upDown(){
this.nextPostList()
}
},
components:{
Previewer
}
}



