
/* eslint-disable */
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import ContactAsk from '@/components/base/contact-ask'

export default {
name: 'app',
data () {
return {
marqueeList: ['<h3>信阳365服务</h3>','<h3>家电百科知识库</h3>'],
categoryList: ['热点', '分类', '服务','视频','圈子'],
navigation: ['/article','/category','/shop','/video','/circle'],
initSelected: 0,
lineWidth: 0,
cultureDialog: false
}
},
created() {
},
methods: {
makePhone(phone) {
window.location.href = 'tel://'+phone
},
subscribeUs(){
this.alert()
},
contactUs(){
this.alert()
},
alert(){
this.$vux.alert.show({
title: '↓长按关注↓',
content: '<img width="50%" src="http://luosen365.com/image/20180512/1526119359358.jpg"/>',
})
}
},
components: {
Tab,
TabItem,
Marquee,
XDialog,
MarqueeItem,
ContactAsk
}
}



