
/* eslint-disable */
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import { getSwiperList, getArticleList } from '@/api/article'
import { RESPONSE_CODE, SERVER_NAME } from '@/api/config'
import { mapMutations } from 'vuex'
import { loadViews } from '@/api/cache'

export default {
data () {
return {
messageList: ['推荐就免费上门清洗油烟机、空调、冰柜等生活家电!!!','凡是通过该平台到门店维修一律免费维修!!!','公告：由于系统升级，评论留言将删选。'],
initSelected: 0,
swiperList: [],
spinnerType: 'lines',
loading: true,
articleList: [],
pullup: true,
tip: '努力加载中...'
}
},
created() {
this._initSwiper()
this._initArticle()
},
methods: {
selectItem (item,index) {

this.$router.push({
path: `article/${item.id}`
})

this.setNews(item);
},
_initSwiper() {
this.swiperList = getSwiperList().map((item, index) => ({
url: item.url,
img: item.picUrl,
fallbackImg: item.fallbackImg,
title: item.title
}))
},
_initArticle() {
this.page = 0
this.size = 8
getArticleList(this.page, this.size).then((res) => {

if( !res) {
this.loading = false
this.tip = '网络不给力！'
return;
}

if (res.code === RESPONSE_CODE) {

this.articleList = res.data.list
this.loading = res.data.hasNext

this.tip = this.loading ? '努力加载中...' : '没有更多数据~'

}
})
},
upDown () {
if (!this.loading) {
return
}
this.page++
getArticleList(this.page, this.size).then((res) => {
if (res.code === RESPONSE_CODE) {

this.articleList = this.articleList.concat(res.data.list)
this.loading = res.data.hasNext

this.tip = this.loading ? '努力加载中...' : '没有更多数据~'
}
})
},
...mapMutations({
setNews: 'SET_NEWS'
})
},
components: {
Swiper,
SwiperItem
}
}



