/* eslint-disable */
import { mapState } from 'vuex'
export default {
  props: ['url'],
  data () {
    return {
      files: [], // 文件缓存
      index: 0, // 序列号
      maxLength: 3 // 图片最大数量
    }
  },
  computed: {
    ...mapState({
      imgStore: state => state.imgstore.img_upload_cache.slice(0, 3),
      imgPaths: state => state.imgstore.img_paths,
      imgStatus: state => state.imgstore.img_status
    })
  },
  methods: {
    // 选择图片
    selectImgs () {
      let fileList = this.$refs.file.files
      console.log(fileList)
      for (let i = 0, len = fileList.length; i < len; i++) {
        let item = {
          key: this.index++,
          name: fileList[i].name,
          size: fileList[i].size,
          file: fileList[i]
        }
        // 将图片文件转成BASE64格式
        let reader = new FileReader()
        reader.onload = (e) => {
          this.$set(item, 'src', e.target.result)
        }
        reader.readAsDataURL(fileList[i])
        this.files.push(item)
      }
      this.files.splice(3)
      this.$store.commit('set_img_upload_cache', this.files.slice(0, 3)) // 存储文件缓存
      this.$store.commit('set_img_status', 'selected') // 更新文件上传状态
    },
    // 上传图片
    submit () {
      let formData = new FormData()
      this.imgStore.forEach((item, index) => {
        item.name = 'imgFiles'
        formData.append(item.name, item.file)
      })
      // 新建请求
      const xhr = new XMLHttpRequest()
      xhr.open('POST', this.url, true)
      xhr.send(formData)
      xhr.onload = () => {
        if (xhr.status === 200 || xhr.status === 304) {
          let datas = JSON.parse(xhr.responseText)
          //console.log('response: ', datas)
          // 存储返回的地址
          let imgUrlPaths = []
          if( datas.code === 0) {
            datas.data.forEach(e => { // error === 0为成功状态
              imgUrlPaths.push(e.fileName)
            })
          }
          
          
          this.$store.commit('set_img_paths', imgUrlPaths) // 存储返回的地址
          this.files = [] // 清空文件缓存
          this.index = 0 // 初始化序列号
          this.$store.commit('set_img_status', 'finished') // 更新文件上传状态
        } else {
          this.$vux.toast.show({
              text: `${xhr.status} 请求错误!`, 
              position: 'middle',
              time: 1000,
              type: 'warn'
          })
        }
      }
    },
    // 移除图片
    remove (index) {
      this.files.splice(index, 1)
      this.$store.commit('set_img_upload_cache', this.files) // 更新存储文件缓存
    },
    // 清空图片
    empty () {
      this.files.splice(0, this.files.length)
      this.$store.commit('set_img_upload_cache', this.files) // 更新存储文件缓存
      this.$store.commit('set_img_paths', [])
    }
  },
  beforeCreate () {
    this.$store.commit('set_img_status', 'ready') // 更新文件上传状态
  },
  destroyed () {
    this.$store.commit('set_img_upload_cache', [])
    this.$store.commit('set_img_paths', [])
  },
  watch: {
    imgStatus () {
      if (this.imgStatus === 'uploading') {
        this.submit()
      }
    },
    imgStore () {
      if (this.imgStore.length <= 0) {
        this.$store.commit('set_img_status', 'ready') // 更新文件上传状态
      }
    }
  }
}



