/* eslint-disable */
import { getArticleByCategoryId } from '@/api/article'
import { RESPONSE_CODE } from '@/api/config'
import {mapGetters, mapMutations} from 'vuex'

const size = 8 

export default {
    data () {
        return {
            page: 0,
            pullup: true,
            articleList: [],
            loading: true,
            title: '',
            id: '',
            tip: '努力载入中...'
        }
    },
    created () {
        if (!this.news.id) {
            this.$router.push({
                path: '/category'
            })
            return
        }
        this.title = this.news.title
        this.id = this.news.id
        this._getArticleList()
    },
    methods: {
        selectItem (item) {
            this.$router.push({
                path: `${this.id}/${item.id}`
            })
            this.setNews(item)
        },
        back () {
            this.$router.push({
                path: '/category'
            })
        },
        downMore () {
            if (!this.loading) {
                return
            }
            this.page++
            getArticleByCategoryId(this.id, this.page, this.size).then((res) => {

                if (res.code === RESPONSE_CODE) {
                    this.articleList = this.articleList.concat(res.data.list)
                    this.loading = res.data.hasNext

                    this.tip = this.loading ? '努力加载中...' : '没有更多数据~'
                }
            })
        },
        _getArticleList () {
            this.page = 0
            this.loading = true
            getArticleByCategoryId(this.id, this.page, this.size).then((res) => {
                
                if( !res) {
                    this.loading = false
                    this.tip = '网络不给力！'
                    return;
                }

                if (res.code === RESPONSE_CODE) {
                    this.articleList = res.data.list
                    this.loading = res.data.hasNext

                    this.tip = this.loading ? '努力加载中...' : '没有更多数据~'
                }
            })
        },
        ...mapMutations({
            setNews: 'SET_NEWS'
        })
    },
    computed: {
        ...mapGetters([
            'news'
        ])
    }
}



