
/* eslint-disable */
import {mapGetters} from 'vuex'
import Previewer from 'vux/src/components/previewer/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Badge from 'vux/src/components/badge/index.vue'


export default {
data () {
return {
pullup: true,
canSacle: false,
zoom: 12,
center: null,
circle: {
clickable: true,
center: null,
radius: 200,
fillOpacity: 0.3,
strokeStyle: 'dashed',
fillColor: '#FFFF00',
strokeColor: '#00BFFF'
},
marker: {
position: null,
events: {
click: () => {

if(!this.mywindow.visible) {
return
}

if (this.mywindow.visible === true) {
this.mywindow.visible = false
} else {
this.mywindow.visible = true
}
},
dragend: (e) => {
this.markers[0].position = [e.lnglat.lng, e.lnglat.lat]
}
},
visible: true,
draggable: false
},
mywindow: {
position: null,
content: '<h4>信阳罗森家电维修</div>',
visible: true,
events: {
close () {
if(!this.mywindow.visible) {
return
}

this.mywindow.visible = false

}
}
},
plugin: {
pName: 'Scale',
events: {
init (instance) {

}
}
},
images: [],
options: {
index: 0
}
}
},
computed: {
...mapGetters([
'shop'
])
},
created(){
if(!this.shop.id){
this.$router.push({
path: '/shop'
})
return
}

this._initMap()

setTimeout(() => {
if (!this.$refs.shopDetailScroll) {
return
}
this.$refs.shopDetailScroll.refresh()
}, 500)

let that = this
for(let item in this.shop.picture) {
that.images.push({
src: this.shop.picture[item]
})
}
},
methods: {
_initMap () {
this.center = [Number(this.shop.x).toFixed(8),Number(this.shop.y).toFixed(8)]
this.circle.center = this.center
this.marker.position = this.center
this.mywindow.position = this.center
this.mywindow.content = '<h4>'+this.shop.shopName+'</div>'
},
back () {
this.$router.back()
},
makePhone(phone) {
window.location.href = 'tel://'+phone
},
resizeHeight () {
if (!this.$refs.shopDetailScroll) {
return
}
this.$refs.shopDetailScroll.refresh()
},
show (index) {
if(!this.canSacle) {
return
}
this.$refs.previewer.show(index)
},
loadImage() {

this.canSacle = true
}
},
components: {
Previewer,
XButton,
Spinner,
Blur,
Badge
}
}



